import React from 'react';
import Palette from './Palette';
import styled from 'styled-components';
import { CreatePalette, EditPalette } from '../../modules/dialog';
import Select from '@material-ui/core/Select';
import ToolTip from '../common/ToolTip';

const PalettesWrapper = styled.div`
  border: 2px solid #59564f;
  border-radius: 3px;
  padding: 2px;
`;

const SelectBlock = styled.div`
  width: 100%;
  margin: 8px;
`;

const PaletteBlock = styled.div`
  margin: 16px 4px;
`;

const StyleButton = styled.button`
  width: 40px;
  height: 20px;
`;

const Palettes = ({
  palettes,
  paletteNames,
  selectPaletteId,
  selectPaletteLine,
  handleChange,
  handleSelectLeftColor,
  handleSelectRightColor,
  handleSelectColorCell,
  handleOpenPaletteDialog,
}) => {
  return (
    <PalettesWrapper>
      <SelectBlock>
        <Select
          native
          color="secondary"
          autoWidth={true}
          value={selectPaletteId}
          onChange={handleChange}
          inputProps={{
            name: 'age',
            id: 'age-native-simple',
          }}
        >
          {paletteNames.map((name) => (
            <option key={name.id} value={name.id}>
              {name.name}
            </option>
          ))}
        </Select>
      </SelectBlock>
      <PaletteBlock>
        {palettes.map((palette, idx) => (
          <Palette
            palette={palette}
            key={palette.id}
            idx={idx}
            selectedPalette={selectPaletteLine === idx}
            handleSelectColorCell={handleSelectColorCell}
            handleSelectLeftColor={handleSelectLeftColor}
            handleSelectRightColor={handleSelectRightColor}
          />
        ))}
      </PaletteBlock>
      <ToolTip placement="bottom" tooltip="Create new palette">
        <StyleButton onClick={() => handleOpenPaletteDialog(CreatePalette)}>
          create
        </StyleButton>
      </ToolTip>
      <ToolTip placement="bottom" tooltip="Modify this palette">
        <StyleButton onClick={() => handleOpenPaletteDialog(EditPalette)}>
          edit
        </StyleButton>
      </ToolTip>
    </PalettesWrapper>
  );
};

export default React.memo(Palettes);
